<template>
<div>
<vue-input
  :value="fieldValue"
  :readonly="true"
  :disabled="disabled "
  :clickable="true"
  :clearable="clearable"
  :size="size"
  :autofocus="autofocus"
  :placeholder="placeholder"
  :text-align="textAlign"
  :icon="showClose ? '' + clearIcon : ''"
  @click.native="onClick"
  :on-icon-click="handleClickIcon"
></vue-input>
<popup v-model="showPicker" position="bottom">
<picker
      :class="popperClass"
      :columns="columns"
      :title="title"
      ref="picker"
      :loading="loading"
      :confirm-button-text="confirmButtonText"
      :cancel-button-text="cancelButtonText"
      :value-key="valueKey"
      :value-prop="valueProp"
      :value-disabled="valueDisabled"
      :show-toolbar="true"
      :toolbar-position="toolbarPosition"
      :allow-html="allowHtml"
      :item-height="itemHeight"
      :visible-item-count="visibleItemCount"
      :swipe-duration="swipeDuration"
      @confirm="onConfirm"
      @cancel="onCancel"
      @change="onChange"
    >
      <template v-slot:default>
        <slot name="default"></slot>
      </template>
      <template v-slot:title>
        <slot name="title"></slot>
      </template>
      <template v-slot:columns-top>
        <slot name="columns-top"></slot>
      </template>
      <template v-slot:columns-bottom>
        <slot name="columns-bottom"></slot>
      </template>
    </picker>
</popup>

</div>


</template>

<script>


import Popup from '../popup';
import Picker from '../picker';

export default {
  name: 'VueCascader',
  components: { Picker, Popup },
  props: {
    options: {
      type: Array,
      required: true,
    },
    value: {
      type: Array,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    loading: {
      type: Boolean,
      default: false,
    },
    clearable: {
      type: Boolean,
      default: true,
    },
    autofocus: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
    },
    textAlign: {
      type: String,
    },
    size: {
      type: String,
    },
    popperClass: {
      type: String,
    },
    props: {
      type: Object,
      default() {
        return {
          label: 'label',
          value: 'value',
          disabled: 'disabled',
        };
      },
    },
    title: {
      type: String,
    },
    confirmButtonText: {
      type: String,
      default: '',
    },
    cancelButtonText: {
      type: String,
      default: '',
    },
    toolbarPosition: {
      type: String,
      default: 'top',
    },
    allowHtml: {
      type: Boolean,
      default: true,
    },
    itemHeight: {
      type: [String, Number],
    },
    visibleItemCount: { // 可见的选项个数
      type: Number,
      default: 5,
    },
    swipeDuration: { // 快速滑动时惯性滚动的时长，单位ms
      type: [String, Number],
      default: 1000,
    },
    clearIcon: {//组件最右侧内容清空图标的设置
      type: String,
      default: 'vue-icon-close'
    },
  },
  mounted() {
    this.showPicker = false;
    this.setValues(this.value);
  },
  data() {
    return {
      showPicker: false,
      fieldValue: '',
    };
  },
  methods: {
    setValues(values) {
      if (values && values.length > 0) {
        this.getPicker().setValues(values);
        this.fieldValue = this.getTexts().toString();
      }
    },
    getPicker() {
      return this.$refs.picker;
    },
    onClick() {
       if(!this.disabled){
        this.showPicker = true;
      }
      
    },
    onConfirm(value, index) {
      this.showPicker = false;
      this.$emit('input', this.getPicker().getValues());
      this.$emit('confirm', value, index);
    },
    onCancel(value, index) {
      this.showPicker = false;
      this.$emit('cancel', value, index);
    },
    onChange(picker, values, index) {
      this.$emit('change', picker, values, index);
    },
    getTexts() {
      if (this.getPicker().dataType === 'cascade') {
        return this.getPicker().children.map((child) => {
          const v = child.getValue();
          return v[this.valueKey];
        });
      }
      return this.children.map((child) => child.getValue());
    },
    handleClickIcon: function (event) {
        this.fieldValue = '';
        event.stopPropagation();
        this.$emit('input', null);
      },
  },
  watch: {
    value(val) {
      this.setValues(val);
    },
  },
  computed: {
    columns() {
      return this.options;
    },
    valueProp() {
      return this.props.value || 'value';
    },
    valueKey() {
      return this.props.label || 'label';
    },
    valueDisabled() {
      return this.props.disabled || 'disabled';
    },
    showClose(){
      if(VueUtil.isArray(this.value)){
        return this.value && this.value.length>0;
      }
      return this.value?true:false;
    },
  },
};
</script>
