import './style/base.less';
import './popup/index.less';
import './overlay/index.less';
import './icon/index.less';
import './info/index.less';

import Loading from './loading/index';
import './loading/index.less';

import Picker from './picker/index';
import './picker/index.less';

import ActionSheet from './action-sheet/index';
import './action-sheet/index.less';

import NavBar from './nav-bar/index';
import './nav-bar/index.less';

import Cascader from './cascader/cascader.vue';

import TabBar from './tabbar/index';
import './tabbar/index.less';
import TabBarItem from './tabbar-item/index';
import './tabbar-item/index.less';

//宫格组件
import CellLayout from './cell-layout/index';
import './cell-layout/index.less';
import CellLayoutItem from './cell-layout-item/index';
import './cell-layout-item/index.less';


// import Icon from './icon/index';
// import './icon/index.less';

import TextIcon from './text-icon/index';
import './text-icon/index.less';

import Stepper from './stepper/index';
import './stepper/index.less';

import SwipeCell from './swipe-cell/index';
import './swipe-cell/index.less';
import './utils/touch-emulator/index.js';//引入该文件将mouse事件转换成touch事件

import Skeleton from './skeleton/index';
import './skeleton/index.less';

import IndexAnchor from './index-anchor/index';
import './index-anchor/index.less';
import IndexBar from './index-bar/index';
import './index-bar/index.less';

import ScrollView from './scroll-view/index.vue';
import ScrollViewMore from './scroll-view-more/index.vue';
import ScrollViewRefresh from './scroll-view-refresh/index.vue';

import DatetimePicker from './datetime-picker/datetimePicker.vue';
import DatetimePickerMobile from './datetime-picker/datetimePickerMobile.vue';
import TimePicker from './datetime-picker/timePicker.vue';
import TimePickerMobile from './datetime-picker/timePickerMobile.vue';

import zh from './locale/zh';
import en from './locale/en';
import ja from './locale/ja';

import MobileCssLoader from './MobileCssLoader';

declare const VueUtil: any;

VueUtil.setLocale('zh', zh);
VueUtil.setLocale('en', en);
VueUtil.setLocale('ja', ja);

// 所有组件列表
let components = [

  Loading,
  Picker,
  ActionSheet,
  NavBar,
  TabBar,
  TabBarItem,
  CellLayout,
  CellLayoutItem,
  // Icon,
  TextIcon,
  Stepper,
  SwipeCell,
  Skeleton,
  IndexAnchor,
  IndexBar,
  ScrollView,
  ScrollViewMore,
  ScrollViewRefresh,
];

VueUtil.getSystemInfo().isLoadMobileJs = true;
if (VueUtil.getSystemInfo().device == 'Mobile') {
   
  const copyViyComponents = function getViyComponents(Vue: any) {
    const ary: any[] = [];
    const copyComps = ['VueDatePicker','VueTimePicker'];
    copyComps.map((component) => {
        const ops = Vue.component(component).options;
        ops.name = ops.name + 'Viy';
        ary.push(ops);
      }
      
    );
    return ary;
  };
  components = components.concat(copyViyComponents(window.Vue));
  components.push(Cascader);
  components.push(DatetimePicker);
  components.push(DatetimePickerMobile);
  components.push(TimePicker);
  components.push(TimePickerMobile);

  MobileCssLoader.use();
}

let installed = false;

const install = function install(Vue: any) {
  
  if (installed) return;
  installed = true;
  components.map((component) => Vue.component(component.name, component));
};

if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue);
}

export default {
  install,
  ...components,
};


