<template>
  <div>
    <div v-if="!isRange">
      <vue-input
        :value="fieldValue"
        :readonly="true"
        :disabled="disabled"
        :clickable="true"
        :clearable="clearable"
        :size="size"
        :placeholder="placeholder"
        :text-align="actualTextAlign"
        @click.native="onClick"
        :icon="showClose ? '' + clearIcon : ''"
        :on-icon-click="handleClickIcon"
      >
        <i slot="prefix" :class="['vue-input__icon',triggerClass]"></i>
      </vue-input>
      <popup v-model="showPicker" position="bottom">
        <time-picker
          :format="format"
          :formatter="formatter"
          :filter="filter"
          position="bottom"
          :class="popperClass"
          :title="title"
          ref="picker"
          :value="actualValue"
          type="time"
          :min-hour="mMinHour"
          :max-hour="mMaxHour"
          :min-minute="mMinMinute"
          :max-minute="mMaxMinute"
          :min-second="mMinSecond"
          :max-second="mMaxSecond"
          :default-pick="actualDefaultPick"
          :confirm-button-text="confirmButtonText"
          :cancel-button-text="cancelButtonText"
          :show-toolbar="true"
          :item-height="itemHeight"
          :visible-item-count="visibleItemCount"
          :swipe-duration="swipeDuration"
          @confirm="onConfirm"
          @cancel="onCancel"
          @change="onChange"
        ></time-picker>
      </popup>
    </div>
    <div v-else style="background-color: #fff;">
      <vue-input
        style="width:48%"
        :value="fieldValue"
        :readonly="true"
        :disabled="disabled"
        :placeholder="startPlaceholder || $t('vue.datepicker.startPlaceholder')"
        :clickable="true"
        :clearable="clearable"
        :size="size"
        :text-align="actualTextAlign"
        @click.native="onClick"
      >
        <i slot="prefix" :class="['vue-input__icon',triggerClass]"></i>
      </vue-input>
      <span>{{rangeSeparator}}</span>
      <vue-input
        style="width:48%"
        :value="endFieldValue"
        :readonly="true"
        :disabled="disabled"
        :clickable="true"
        :clearable="clearable"
        :size="size"
        :placeholder="endPlaceholder || $t('vue.datepicker.endPlaceholder')"
        :text-align="actualTextAlign"
        @click.native="endOnClick"
        :icon="showClose ? '' + clearIcon : ''"
        :on-icon-click="handleClickIcon"
      ></vue-input>

      <popup v-model="showPicker" position="bottom">
        <time-picker
          :format="format"
          :formatter="formatter"
          :filter="filter"
          position="bottom"
          :class="popperClass"
          :title="startTitle || $t('vue.datepicker.startTime')"
          :placeholder="startPlaceholder || $t('vue.datepicker.startPlaceholder')"
          ref="picker"
          :value="startValue"
          type="time"
          :min-hour="mMinHour"
          :max-hour="mMaxHour"
          :min-minute="mMinMinute"
          :max-minute="mMaxMinute"
          :min-second="mMinSecond"
          :max-second="mMaxSecond"
          :default-pick="isArray(actualDefaultPick) ? actualDefaultPick[0] : actualDefaultPick"
          :confirm-button-text="confirmButtonText"
          :cancel-button-text="cancelButtonText"
          :show-toolbar="true"
          :item-height="itemHeight"
          :visible-item-count="visibleItemCount"
          :swipe-duration="swipeDuration"
          @confirm="startOnConfirm"
          @cancel="startOnCancel"
          @change="startOnChange"
        ></time-picker>
      </popup>
      <popup v-model="endShowPicker" position="bottom">
        <time-picker
          :format="format"
          :formatter="formatter"
          :filter="filter"
          position="bottom"
          :class="popperClass"
          :title="endTitle || $t('vue.datepicker.endTime')"
          :placeholder="endPlaceholder || $t('vue.datepicker.endPlaceholder')"
          ref="endPicker"
          :value="endValue"
          type="time"
          :min-hour="mMinHour2"
          :max-hour="mMaxHour2"
          :min-minute="mMinMinute2"
          :max-minute="mMaxMinute2"
          :min-second="mMinSecond2"
          :max-second="mMaxSecond2"
          :default-pick="isArray(actualDefaultPick) ? actualDefaultPick[1] : actualDefaultPick"
          :confirm-button-text="confirmButtonText"
          :cancel-button-text="cancelButtonText"
          :show-toolbar="true"
          :item-height="itemHeight"
          :visible-item-count="visibleItemCount"
          :swipe-duration="swipeDuration"
          @confirm="endOnConfirm"
          @cancel="endOnCancel"
          @change="endOnChange"
        ></time-picker>
      </popup>
    </div>
  </div>
</template>

<script>
import Popup from '../popup';
import TimePicker from '../datetime-picker';


export default {
  name: 'VueTimePickerMobile',
  components: { TimePicker, Popup },
  props: {
    disabled: {
      type: Boolean,
      default: false,
    },
    clearable: {
      type: Boolean,
      default: true,
    },
    size: {
      type: String,
    },
    placeholder: {
      type: String,
    },
    startPlaceholder: {
      type: String,
      default:''
    },
    endPlaceholder: {
      type: String,
      default:''
    },
    isRange: {
      type: Boolean,
      default:false
    },
    rangeSeparator: {//选择范围时的分隔符
      type: String,
      default:'-'
    },
    format:{
      type:String,
      default:"HH:mm:ss"
    },
    valueFormat: {
      type: String,
    },
    popperClass: {
      type: String,
    },
    value: {
      type: [Number, String, Date, Array],
    },
    textAlign: {
      type: String
    },
    minHour: {
      type: [Number,String]
    },
    maxHour: {
      type: [Number,String]
    },
    minMinute: {
      type: [Number,String],
    },
    maxMinute: {
      type: [Number,String],
    },
    minSecond: {
      type: [Number,String],
    },
    maxSecond: {
      type: [Number,String],
    },
    prefixIcon: {
      type: String,
    },
    clearIcon: {
        type: String,
        default: 'vue-icon-close'
    },
    title: {
      type: String,
      default:''
    },
    startTitle: {
      type: String,
      default:''
    },
    endTitle: {
      type: String,
      default:''
    },
    confirmButtonText: {
      type: String,
      default: '',
    },
    cancelButtonText: {
      type: String,
      default: '',
    },
    itemHeight: {
      type: [String, Number],
    },
    visibleItemCount: { // 可见的选项个数
      type: Number,
      default: 5,
    },
    swipeDuration: { // 快速滑动时惯性滚动的时长，单位ms
      type: [String, Number],
      default: 1000,
    },
    formatter: {
      type: Function
    },
    filter:{
      type:Function
    },
    defaultPick: {
      type: [Date, String,Number, Array]
    }
  },
  mounted() {
    //this.showPicker = false;
    //this.endShowPicker = false;
  },
  data() {
    return {
      showPicker: false,
      endShowPicker:false,
      fieldValue: '',
      endFieldValue:'',
      actualValue: '',
      startValue:'',
      endValue:'',
      mMinHour:this.minHour,
      mMaxHour:this.maxHour,
      mMinMinute:this.minMinute,
      mMaxMinute:this.maxMinute,
      mMinSecond:this.minSecond,
      mMaxSecond:this.maxSecond,
      mMinHour2:this.minHour,
      mMaxHour2:this.maxHour,
      mMinMinute2:this.minMinute,
      mMaxMinute2:this.maxMinute,
      mMinSecond2:this.minSecond,
      mMaxSecond2:this.maxSecond,
    };
  },
  methods: {
    handleClickIcon: function (event) {
      if(this.disabled){
        return;
      }
        this.fieldValue = '';
        this.endFieldValue = '';
        this.startValue=null;
        this.endValue=null;
        event.stopPropagation();
        this.$emit('input', null);
    },
    onClick() {
      if(this.disabled){
        return;
      }
      if(this.isRange){
        this.mMinHour   = this.minHour;
        this.mMinMinute = this.minMinute;
        this.mMinSecond = this.minSecond;

        if(this.endFieldValue){
          const time = VueUtil.parseDate(this.endFieldValue, this.format);
          this.mMaxHour   = time.getHours();
          this.mMaxMinute = time.getMinutes();
          this.mMaxSecond = time.getSeconds();
        }else{
          this.mMaxHour   = this.maxHour;
          this.mMaxMinute = this.maxMinute;
          this.mMaxSecond = this.maxSecond;
        }
      }
      this.showPicker = true;
    },
    formatInnerValueByValueFormate(val) {

      if (!val) return;
      
      const innerFormat = ['HH', 'HH:mm', 'HH:mm:ss'][val.split(':').length - 1];
      const date = VueUtil.parseDate(val, innerFormat);

      if (this.valueFormat) {
        return VueUtil.formatDate(date, this.valueFormat);
      } else {
        return date;
      }
    },
    onConfirm(value, index) {
      this.showPicker = false;
      value = this.formatInnerValueByValueFormate(value);
      
      this.$emit('input', value);
      this.$emit('confirm', value, index);
    },
    onCancel(value, index) {
      this.showPicker = false;
      this.$emit('cancel', value, index);
    },
    onChange(picker, value, index) {
      this.$emit('change', picker, value, index);
    },
    endOnClick() {
      if(this.disabled){
        return;
      }
      if(this.fieldValue){
        const time = VueUtil.parseDate(this.fieldValue, this.format);
        this.mMinHour2   = time.getHours();
        this.mMinMinute2 = time.getMinutes();
        this.mMinSecond2 = time.getSeconds();
      }else{
        this.mMinHour2   = this.minHour;
        this.mMinMinute2 = this.minMinute;
        this.mMinSecond2 = this.minSecond;
      }
      this.mMaxHour2   = this.maxHour;
      this.mMaxMinute2 = this.maxMinute;
      this.mMaxSecond2 = this.maxSecond;
      this.endShowPicker = true;
    },
    startOnConfirm(value, index) {
      if(this.endValue&&value>this.endValue){
        console.error('The start time cannot be later than the end time!');
        return;
      }
      this.showPicker = false;
      const endVal = this.value?this.value[1]:null;
      this.$emit('input', [this.formatInnerValueByValueFormate(value),endVal]);
     
      this.$emit('start-confirm', this.formatInnerValueByValueFormate(value), index);
    },
    startOnCancel(value, index) {
      this.showPicker = false;
      this.$emit('start-cancel', value, index);
    },
    startOnChange(picker, value, index) {
      this.$emit('start-change', picker, value, index);
    },
    endOnConfirm(value, index) {
      if(this.startValue&&value<this.startValue){
        console.error('The end time cannot be earlier than the start time!');
        return;
      }
      this.endShowPicker = false;
      const startVal = this.value?this.value[0]:null
      this.$emit('input', [startVal,this.formatInnerValueByValueFormate(value)]);

      this.$emit('end-confirm', this.formatInnerValueByValueFormate(value), index);
    },
    endOnCancel(value, index) {
      this.endShowPicker = false;
      this.$emit('end-cancel', value, index);
    },
    endOnChange(picker, value, index) {
      this.$emit('end-change', picker, value, index);
    },
    isArray: Array.isArray
  },
  watch: {
    value: {
      handler: function(val) {
        if(VueUtil.isArray(val)){
          let startDate = val[0];
          let endDate = val[1];
          if (this.valueFormat) {
            if(val[0] && !VueUtil.isDate(val[0])) {
              startDate = VueUtil.parseDate(val[0], this.valueFormat);
            }
            if(val[1] && !VueUtil.isDate(val[1])) {
              endDate = VueUtil.parseDate(val[1], this.valueFormat);
            }
          }

          this.fieldValue = VueUtil.formatDate(startDate, this.format);
          this.startValue = VueUtil.formatDate(startDate, 'HH:mm:ss');

          this.endFieldValue = VueUtil.formatDate(endDate, this.format);
          this.endValue = VueUtil.formatDate(endDate, 'HH:mm:ss');
        }else{
          if (val && this.valueFormat) {
            val = VueUtil.parseDate(val, this.valueFormat);
          }
          this.fieldValue = VueUtil.formatDate(val, this.format);
          this.actualValue = VueUtil.formatDate(val, 'HH:mm:ss');
        }
      },
      immediate: true
    },
  },
  computed: {
    showClose(){
      if(VueUtil.isArray(this.value)){
        return this.value && this.value.length>0;
      }
      return this.value?true:false;
    },
    triggerClass() {
      return this.prefixIcon || 'vue-icon-time';
    },
    actualTextAlign() {
       return this.textAlign ? this.textAlign : this.isRange ? 'center' :  'left';
    },
    actualDefaultPick() {
      if (this.defaultPick) {
        if (Array.isArray(this.defaultPick)) {
          return this.defaultPick.map(defaultPick => {
            const date = this.valueFormat ? VueUtil.parseDate(defaultPick, this.valueFormat) : defaultPick;
            return VueUtil.formatDate(date, 'HH:mm:ss');
          });
        } else {
          const date = this.valueFormat ? VueUtil.parseDate(this.defaultPick, this.valueFormat) : this.defaultPick;
          return VueUtil.formatDate(date, 'HH:mm:ss');
        }
      }
      return this.defaultPick;
    }
  },
};
</script>
