<template>
  <div>
    <div v-if="!isRange">
      <vue-input
        :value="fieldValue"
        :readonly="true"
        :disabled="disabled"
        :clickable="true"
        :clearable="clearable"
        :size="size"
        :placeholder="placeholder"
        :text-align="actualTextAlign"
        :icon="showClose ? '' + clearIcon : ''"
        @click.native="onClick"
        :on-icon-click="handleClickIcon">
        <i slot="prefix" :class="['vue-input__icon',triggerClass]"></i>
      </vue-input>
      <popup v-model="showPicker" position="bottom">
        <datetime-picker
          :format="format"
          :value-format="valueFormat"
          :formatter="actualFormatter"
          :filter="filter"
          position="bottom"
          :class="popperClass"
          :title="title"
          ref="picker"
          :value="value"
          :type="type"
          :min-date="mMinDate"
          :max-date="mMaxDate"
          :default-pick="actualDefaultPick"
          :confirm-button-text="confirmButtonText"
          :cancel-button-text="cancelButtonText"
          :show-toolbar="true"
          :item-height="itemHeight"
          :visible-item-count="visibleItemCount"
          :swipe-duration="swipeDuration"
          :picker-options="pickerOptions"
          @confirm="onConfirm"
          @cancel="onCancel"
          @change="onChange"
        >
        </datetime-picker>
      </popup>
    </div>
    <div v-else style="background-color: #fff;">
          <vue-input style="width:48%"
            :value="fieldValue"
            :readonly="true"
            :disabled="disabled"
            :placeholder="startPlaceholder || $t('vue.datepicker.startPlaceholder')"
            :clickable="true"
            :clearable="clearable"
            :size="size"
            :text-align="actualTextAlign"
            @click.native="onClick">
            <i slot="prefix" :class="['vue-input__icon',triggerClass]"></i>
          </vue-input>
         <span >{{rangeSeparator}}</span>
            <vue-input style="width:48%"
                :value="endFieldValue"
                :readonly="true"
                :disabled="disabled"
                :clickable="true"
                :clearable="clearable"
                :size="size"
                :placeholder="endPlaceholder || $t('vue.datepicker.endPlaceholder')"
                :text-align="actualTextAlign"
                @click.native="endOnClick"
                :icon="showClose ? '' + clearIcon : ''"
                :on-icon-click="handleClickIcon">
            </vue-input>

        <popup v-model="showPicker" position="bottom">
        <datetime-picker
          :format="format"
          :value-format="valueFormat"
          :formatter="actualFormatter"
          :filter="filter"
          position="bottom"
          :class="popperClass"
          :title="startTitle || $t('vue.datepicker.startDate')"
          :placeholder="startPlaceholder || $t('vue.datepicker.startPlaceholder')"
          ref="picker"
          :value="startValue"
          :type="type"
          :min-date="mMinDate"
          :max-date="mMaxDate"
          :default-pick="isArray(actualDefaultPick) ? actualDefaultPick[0] : actualDefaultPick"
          :confirm-button-text="confirmButtonText"
          :cancel-button-text="cancelButtonText"
          :show-toolbar="true"
          :item-height="itemHeight"
          :visible-item-count="visibleItemCount"
          :swipe-duration="swipeDuration"
          @confirm="startOnConfirm"
          @cancel="startOnCancel"
          @change="startOnChange">
        </datetime-picker>
      </popup>    
        <popup v-model="endShowPicker" position="bottom">
          <datetime-picker
            :format="format"
            :value-format="valueFormat"
            :formatter="actualFormatter"
            :filter="filter"
            position="bottom"
            :class="popperClass"
            :title="endTitle || $t('vue.datepicker.endDate')"
            :placeholder="endPlaceholder || $t('vue.datepicker.endPlaceholder')"
            ref="endPicker"
            :value="endValue"
            :type="type"
            :min-date="mMinDate2"
            :max-date="mMaxDate2"
            :default-pick="isArray(actualDefaultPick) ? actualDefaultPick[1] : actualDefaultPick"
            :confirm-button-text="confirmButtonText"
            :cancel-button-text="cancelButtonText"
            :show-toolbar="true"
            :item-height="itemHeight"
            :visible-item-count="visibleItemCount"
            :swipe-duration="swipeDuration"
            @confirm="endOnConfirm"
            @cancel="endOnCancel"
            @change="endOnChange">
          </datetime-picker>
        </popup>

        </div>

  </div>
    
  
</template>

<script>
import Popup from '../popup';
import DatetimePicker from '../datetime-picker';
import { getMonthWeekNumByDate } from './shared';


export default {
  name: 'VueDatePickerMobile',
  components: { DatetimePicker, Popup },
  props: {
    disabled: {
      type: Boolean,
      default: false,
    },
    clearable: {
      type: Boolean,
      default: true,
    },
    size: {
      type: String,
    },
    placeholder: {
      type: String,
    },
    startPlaceholder: {
      type: String,
      default:''
    },
    endPlaceholder: {
      type: String,
      default:''
    },
    type: {
      type: String,
      default: 'date',
    },
    format: { //针对的是input最终显示值的格式定义和时间选项的格式定义
      type: String,
    },
    popperClass: {
      type: String,
    },
    rangeSeparator: {//选择范围时的分隔符
      type: String,
      default:'-'
    },
    valueFormat: { //针对的是绑定值value的格式定义
      type: String,
    },
    pickerOptions: {
    },
    value: {//选择器当前选中值(通过v-model双向绑定使用)
      type: [Number, String,Date,Array],
    },
    textAlign: {//输入框文字水平对齐方式,当为区间类型时文本默认居中对齐，否则默认居左对齐
      type: String,
    },
    minDate: {//选择器可选范围的最小日期
      type: Date,
    },
    maxDate: {//选择器可选范围的最大日期
      type: Date,
    },
    prefixIcon: {//组件最左侧图标的设置
      type: String,
    },
    clearIcon: {//组件最右侧内容清空图标的设置
        type: String,
        default: 'vue-icon-close'
    },
    title: {
      type: String,
      default:''
    },
    startTitle: {
      type: String,
      default:''
    },
    endTitle: {
      type: String,
      default:''
    },
    confirmButtonText: {
      type: String,
      default: '',
    },
    cancelButtonText: {
      type: String,
      default: '',
    },
    itemHeight: {
      type: [String, Number],
    },
    visibleItemCount: { // 可见的选项个数
      type: Number,
      default: 5,
    },
    swipeDuration: { // 快速滑动时惯性滚动的时长，单位ms
      type: [String, Number],
      default: 1000,
    },
    formatter: {
      type: Function,
    },
    filter:{
      type:Function
    },
    defaultPick: {
      type: [Date, String,Number, Array]
    }
  },
  mounted() {
    //this.showPicker = false;
    //this.endShowPicker = false;
  },
  data() {
    return {
      showPicker: false,
      endShowPicker:false,
      fieldValue: '',
      endFieldValue:'',
      startValue:'',
      endValue:'',
      mMinDate:this.minDate,
      mMaxDate:this.maxDate,
      mMinDate2:this.minDate?this.minDate:undefined,
      mMaxDate2:this.maxDate?this.maxDate:undefined,
    };
  },
  methods: {
      handleClickIcon: function (event) {
        this.fieldValue = '';
        this.endFieldValue = '';
        this.startValue=null;
        this.endValue=null;
        event.stopPropagation();
        this.$emit('input', null);
      },
     actualFormatter(type, val, date) {
      
       if(this.formatter){
         return this.formatter(type,val, date);
       }

      if (type === 'week') {
        return this.$t('vue.datepicker.weekNumber', {number: val});
      } 
      return val;
    }, 
    onClick() {
      if(!this.disabled){
        this.mMinDate = this.minDate;
        if(this.endFieldValue)
          this.mMaxDate = VueUtil.parseDate(this.endFieldValue, this.actualFormat);
        else
          this.mMaxDate = this.maxDate;
        this.showPicker = true;
      }
      
    },
    onConfirm(value, index) {
      this.showPicker = false;
      const actualValue = this.valueFormat && VueUtil.isDate(value) ? VueUtil.formatDate(value, this.valueFormat) : value;
      this.$emit('input', actualValue);
      this.$emit('confirm', value, index);
    },
    onCancel(value, index) {
      this.showPicker = false;
      this.$emit('cancel', value, index);
    },
    onChange(picker, value, index) {
      this.$emit('change', picker, value, index);
    },
    endOnClick() {
      if(!this.disabled){
        if(this.fieldValue)
          this.mMinDate2 = VueUtil.parseDate(this.fieldValue, this.actualFormat);
        else
          this.mMinDate2 = this.minDate;
        this.mMaxDate2   = this.maxDate;
        this.endShowPicker = true;
      }
      
    },
    startOnConfirm(value, index) {
      if(this.endValue&&new Date(value).getTime()>new Date(this.endValue).getTime()){
        console.error('The start date cannot be later than the end date!');
        return;
      }
      
      this.showPicker = false;
      const actualValue = this.valueFormat && VueUtil.isDate(value) ? [ VueUtil.formatDate(value, this.valueFormat),this.endValue] : [value,this.endValue];
      this.$emit('input', actualValue);
      this.$emit('start-confirm', value, index);
    },
    startOnCancel(value, index) {
      this.showPicker = false;
      this.$emit('start-cancel', value, index);
    },
    startOnChange(picker, value, index) {
      this.$emit('start-change', picker, value, index);
    },
    endOnConfirm(value, index) {
      if(this.startValue&&new Date(value).getTime()<new Date(this.startValue).getTime()){
        console.error('The end date cannot be earlier than the start date!');
        return;
      }

      this.endShowPicker = false;
      const actualValue = this.valueFormat && VueUtil.isDate(value) ? [this.startValue, VueUtil.formatDate(value, this.valueFormat)] : [this.startValue,value];
      this.$emit('input', actualValue);
      this.$emit('end-confirm', value, index);
    },
    endOnCancel(value, index) {
      this.endShowPicker = false;
      this.$emit('end-cancel', value, index);
    },
    endOnChange(picker, value, index) {
      this.$emit('end-change', picker, value, index);
    },
    isArray: Array.isArray,
  },
    watch: {
      value: {
      handler: function(val) {
        if(VueUtil.isArray(val)){
          const startVal     = val[0];
          const endVal       = val[1];

          this.startValue    = this.valueFormat && startVal && VueUtil.isDate(startVal) ? VueUtil.formatDate(startVal, this.valueFormat) : startVal;
          if(startVal){
            if(VueUtil.isDate(startVal)){
              this.fieldValue = VueUtil.formatDate(startVal, this.actualFormat)
            }else{
              this.fieldValue = VueUtil.formatDate(VueUtil.parseDate(startVal, this.valueFormat),this.actualFormat);
            }
          }else{
            this.fieldValue = startVal;
          }

          this.endValue      = this.valueFormat && endVal && VueUtil.isDate(endVal) ? VueUtil.formatDate(endVal, this.valueFormat) : endVal;
          if(endVal){
            if(VueUtil.isDate(endVal)){
              this.endFieldValue = VueUtil.formatDate(endVal, this.actualFormat)
            }else{
              this.endFieldValue = VueUtil.formatDate(VueUtil.parseDate(endVal, this.valueFormat),this.actualFormat);
            }
          }else{
            this.endFieldValue = endVal;
          }
        }else{
          val = this.valueFormat && val && !VueUtil.isDate(val) ? VueUtil.parseDate(val, this.valueFormat) : val;
          let str = VueUtil.formatDate(val, this.actualFormat);
          
          if(this.type==='week' && VueUtil.isDate(val)){
              const year = val.getFullYear();
              const yearWeekNum = VueUtil.getWeekNumber(val);

              if(this.format){
                if(this.format.indexOf('MM')>=0 && this.format.indexOf('ww')>=0){
                  const month = val.getMonth() + 1;
                  const monthWeekNum = getMonthWeekNumByDate(str,this.actualFirstDayOfWeek);
                  str = this.format.replace('yyyy', year);
                  str = this.format.indexOf('yyyy')>=0 ? str.replace('MM', month):this.format.replace('MM',month);
                  str = str.replace('ww', monthWeekNum);
                }
                else{
                  str = this.format.replace('yyyy', year);
                  str = this.format.indexOf('yyyy')>=0 ? str.replace('WW', yearWeekNum) :this.format.replace('WW', yearWeekNum);
                }
              }else{
                const time = this.$t('vue.datepicker.defaultWeekFormat', {year, yearWeekNum});
                str = time;
              }
        }
        this.fieldValue =str;
      }
      },
      immediate: true
    }
  },
  computed: {

    showClose(){
      if(VueUtil.isArray(this.value)){
        return this.value && this.value.length>0;
      }
      return this.value?true:false;
    },
    isRange(){
      if(this.type==='daterange' || this.type==='datetimerange' || this.type==='monthrange'){
        return true;
      }
      return false;
    },
    actualFormat(){
      if(this.type==='week'){
        return 'yyyy-MM-dd';
      }
      if(this.format){
        return this.format;
      }
      let fmt = 'yyyy-MM-dd';
      if(this.type==='year'){
         fmt = 'yyyy';
      }else if(this.type==='month' || this.type === 'monthrange'){
         fmt = 'yyyy-MM';
      }else if(this.type==='date'||this.type==='daterange'){
         fmt = 'yyyy-MM-dd';
      }else if(this.type==='datetime'||this.type==='datetimerange'){
         fmt = 'yyyy-MM-dd HH:mm:ss';
      }else{
        console.error('type of "'+this.type+'" is invalid!');
      }
      return fmt;
    },
    actualFirstDayOfWeek(){
       return this.pickerOptions == undefined || this.pickerOptions.firstDayOfWeek == undefined ? 7 : this.pickerOptions.firstDayOfWeek%7;
    },
    triggerClass() {
      return this.prefixIcon || (this.type.indexOf('time') !== -1 ? 'vue-icon-time' : 'vue-icon-date');
    },
    actualTextAlign() {
       return this.textAlign ? this.textAlign : this.isRange ? 'center' :  'left';
    },
    actualDefaultPick() {

      if (this.defaultPick && this.valueFormat) {
        if (Array.isArray(this.defaultPick)) {
          return this.defaultPick.map(defaultPick => VueUtil.parseDate(defaultPick, this.valueFormat));
        } else if (typeof this.defaultPick === 'string' || typeof this.defaultPick === 'number') {
          return VueUtil.parseDate(this.defaultPick, this.valueFormat);
        }
      }
      return this.defaultPick;
    }
  },
};
</script>
