import { times } from './utils';
import { padZero } from '../utils/format/string';
import { pickerProps } from '../picker/shared';
import Picker from '../picker';

export const sharedProps = {
  ...pickerProps,
  value: null,
  filter: Function,
  showToolbar: {
    type: Boolean,
    default: true,
  },
  formatter: {
    type: Function,
    default: (type, value) => value,
  },
  pickerOptions: {
    firstDayOfWeek:{//周起始日,可选值: 1~7
      type:Number,
      default:7,
    }
  },
  defaultPick: {
    type: [Date, String,Number, Array]
  }
};

export const TimePickerMixin = {
  data() {
    return {
      innerValue: this.formatValue(this.value),
    };
  },

  computed: {
    originColumns() {
      return this.ranges.map(({ type, range: rangeArr }) => {

        let values = times(rangeArr[1] - rangeArr[0] + 1, (index) => {
          if(type=='week'){
            return rangeArr[0] + index;
          }else{
            const value = padZero(rangeArr[0] + index);
            return value;
          }
        });

        if (this.filter) {
          values = this.filter(type, values);
        }

        return {
          type,
          values,
        };
      });
    },

    columns() {
      
      return this.originColumns.map((column) => ({
        values: column.values.map((value) =>
          this.formatter(column.type, value, this.innerValue)
        ),
      }));
    },
  },

  watch: {
    columns: 'updateColumnValue',

    innerValue(val) {
      this.$emit('input', val);
    },
  },

  mounted() {
    if (!this.value && this.defaultPick) {
      this.innerValue = this.defaultPick;
    }
    this.updateColumnValue();

    this.$nextTick(() => {
      this.updateInnerValue();
    });
  },

  methods: {
    // @exposed-api
    getPicker() {
      return this.$refs.picker;
    },

    onConfirm() {
      this.$emit('confirm', this.innerValue);
    },

    onCancel() {
      this.$emit('cancel');
    },
  },

  render() {
    const props = {};
    Object.keys(pickerProps).forEach((key) => {
      props[key] = this[key];
    });

    return (
      <Picker
        ref="picker"
        columns={this.columns}
        onChange={this.onChange}
        onConfirm={this.onConfirm}
        onCancel={this.onCancel}
        {...{ props }}
      />
    );
  },
};

export function getMonthWeekNumByDate(str,actualFirstDayOfWeek){
      str = Date.parse(str);
      const date = new Date(str);
      let year = date.getFullYear();
      let month = date.getMonth() + 1;
      let dateN=new Date(str);
      //月份第一天
      dateN=new Date(dateN.setDate(1));
      const w1=dateN.getDay();
      // 将字符串转为标准时间格式
      let w = date.getDay();//周几
      //if(w===0&&w1!=0){//当月第一天不是周天，当前日期是周天
     if(w===actualFirstDayOfWeek&&w1!=actualFirstDayOfWeek){//当月第一天不是firstDayOfWeek，当前日期是firstDayOfWeek
        w=7;
      }
      let week = Math.ceil((date.getDate() + 6 - w) / 7);
      //if(w1!=0){//当月第一天不是周天
      if(w1!=actualFirstDayOfWeek){//当月第一天不是firstDayOfWeek
        //week = Math.ceil((date.getDate() + 6 - w) / 7) - 1;
        week = Math.ceil((date.getDate() + 6 - w) / 7)  ;
      }
      if (week === 0) {//第0周归于上月的最后一周
        month = date.getMonth();
        if(month===0){//跨年
          month=12;
          year=year-1;
        }
        //const dateLast = new Date(date);
        const dayLast = new Date(year, month, 0).getDate();
        const timestamp = new Date(year, month - 1, dayLast);
        w = new Date(timestamp).getDay();//周几
        //if (w === 0) {
        if(w === actualFirstDayOfWeek){
          w = 7;
        }
        week = Math.ceil((timestamp.getDate() + 6 - w) / 7) - 1;
      }
     // week=cNum[week];
      //const time = year+"年"+month + "月第" + week + "周";
      //console.log(time);
      return week;
}
