// Utils
import { createNamespace, addUnit } from '../utils';

// Components
import Icon from '../icon';

const [createComponent, bem] = createNamespace('text-icon');

export default createComponent({
  props: {
    iconName:String,
    iconColor:String,
    iconSize:  String | Number,
    dot:{type: Boolean, default:false },
    badge: String | Number,

    label: String,
    labelColor:String,
    labelSize: String | Number,
    labelPosition: {type:String, default: 'bottom'},
    labelPadding: {type:String | Number, default: 2},
  },

  data() {
    return {
    };
  },
  beforeCreate(){
    const createEmitter = (eventName) => (event) =>
    this.$emit(eventName, event);

    this.handClick = createEmitter('click');
  },
  methods: {
    labelPaddingType() {
      // return 'paddingLeft';
      return this.labelPosition == 'bottom' ? 'paddingTop' : this.labelPosition == 'top' ? 'paddingBottom' :this.labelPosition == 'right' ? 'paddingLeft' : 'paddingRight'; 
    },
    genTextPosition() {
      return [
        <Icon name={this.iconName} color={this.iconColor} size={this.iconSize} dot={this.dot} badge={this.badge}/>,
        <span style={{ color: this.labelColor, fontSize: addUnit(this.labelSize),[this.labelPaddingType()]:addUnit(this.labelPadding)}} 
        class={bem('label')}>{this.label}</span>,
      ];
    },
  },

  render() {
    return (
      <div
        ref="textIcon"
        class={[bem(),bem(this.labelPosition)]}
        onClick={this.handClick}
      >
      {this.genTextPosition()}      
      </div>
    );
  },
});
