import { createNamespace } from '../utils';
import { padZero } from '../utils/format/string';
import { range } from '../utils/format/number';
import { sharedProps, TimePickerMixin } from './shared';

const [createComponent] = createNamespace('time-picker');

export default createComponent({
  mixins: [TimePickerMixin],

  props: {
    ...sharedProps,
    minHour: {
      type: [Number, String],
      default: 0,
    },
    maxHour: {
      type: [Number, String],
      default: 23,
    },
    minMinute: {
      type: [Number, String],
      default: 0,
    },
    maxMinute: {
      type: [Number, String],
      default: 59,
    },
    minSecond: {
      type: [Number, String],
      default: 0,
    },
    maxSecond: {
      type: [Number, String],
      default: 59,
    },
    format:{
      type:String,
      default:"HH:mm:ss"
    }
  },

  computed: {
   
    ranges() {
      const hourRange = {
        type: 'hour',
        range: [this.minHour, this.maxHour],
      };
      const minuteRange = {
        type: 'minute',
        range: [this.minMinute, this.maxMinute],
      };
      const secondRange = {
        type: 'second',
        range: [this.minSecond, this.maxSecond],
      };

      const resultData = [];
      if(this.format.indexOf("HH")>=0)
        resultData.push(hourRange);
      if(this.format.indexOf("mm")>=0)
        resultData.push(minuteRange);
      if(this.format.indexOf("ss")>=0)
        resultData.push(secondRange);

      return resultData;
    },
  },

  watch: {
    filter: 'updateInnerValue',
    minHour: 'updateInnerValue',
    maxHour: 'updateInnerValue',
    minMinute: 'updateInnerValue',
    maxMinute: 'updateInnerValue',
    minSecond: 'updateInnerValue',
    maxSecond: 'updateInnerValue',

    value(val) {
      // 清空后值为null，不重新设置picker的选中项
      if (val === null) return;
      val = this.formatValue(val);

      if (val !== this.innerValue) {
        this.innerValue = val;
        this.updateColumnValue();
      }
    },
  },

  methods: {
    formatValue(value) {
      if (!value) {
        const currDate = new Date();//设置默认时间为当前时间
        const hour   = currDate.getHours();
        const minute = currDate.getMinutes();
        const second = currDate.getSeconds();
        value = `${padZero(hour)}:${padZero(minute)}:${padZero(second)}`;
        // value = `${padZero(this.minHour)}:${padZero(this.minMinute)}:${padZero(this.minSecond)}`;
      }

      let [hour, minute,second] = value.split(':');
      hour   = hour ? padZero(range(hour, this.minHour, this.maxHour)) : undefined;
      minute = minute ? padZero(range(minute, this.minMinute, this.maxMinute)): undefined;
      second = second ? padZero(range(second, this.minSecond, this.maxSecond)) : undefined;

      let result = hour ? `${hour}` : "";
      result += minute ? `:${minute}` : "";
      result += second ? `:${second}` : "";
      return result;
    },

    updateInnerValue() {
      let hour,minute,second = undefined;
      for(let i=0;i<this.originColumns.length;i++){
        const tmpType = this.originColumns[i].type;
        const tmpVal  = this.getPicker().getValues()[i].replace(/[^0-9]/ig,"");//parseInt(this.getPicker().getValues()[i])?parseInt(this.getPicker().getValues()[i]):"00";
        if(tmpType == "hour"){
          hour = tmpVal;
        }else if(tmpType == "minute"){
          minute = tmpVal;
        }else if(tmpType == "second"){
          second = tmpVal;
        }
      }
      let formatData = hour ? `${hour}` : "";
      formatData += minute ? `:${minute}` : "";
      formatData += second ? `:${second}` : "";
      this.innerValue = this.formatValue(formatData);
      this.updateColumnValue();
    },

    onChange(picker) {
      this.updateInnerValue();

      this.$nextTick(() => {
        this.$nextTick(() => {
          this.$emit('change', picker);
        });
      });
    },

    updateColumnValue() {
      const { formatter } = this;
      const pair = this.innerValue.split(':');
      const values = [];
      if(pair[0])
        values.push(formatter('hour', pair[0], pair));
      if(pair[1])
        values.push(formatter('minute', pair[1], pair));
      if(pair[2])
        values.push(formatter('second', pair[2], pair));

      this.$nextTick(() => {
        this.getPicker().setValues(values);
      });
    },
  },
});
