import { createNamespace, addUnit } from '../utils';
import { BORDER_TOP } from '../utils/constant';
import { ParentMixin } from '../mixins/relation';

const [createComponent, bem] = createNamespace('cell-layout');

export default createComponent({
  mixins: [ParentMixin('vueCellLayout')],

  props: {
    columnNum: {
      type: [Number, String],
      default: 4,
    },
    iconSize: [Number, String],
    gutter: [Number, String],
    border: {
      type: Boolean,
      default: true,
    },
    center: {
      type: Boolean,
      default: true,
    },
    square: Boolean,
    clickable: Boolean,
  },

  computed: {
    style() {
      const { gutter } = this;

      if (gutter) {
        return {
          paddingLeft: addUnit(gutter),
        };
      }
    },
  },

  render() {
    return (
      <div
        style={this.style}
        class={[bem(), { [BORDER_TOP]: this.border && !this.gutter }]}
      >
        {this.slots()}
      </div>
    );
  },
});
