import { createNamespace } from '../utils';
import { isDate } from '../utils/validate/date';
import { padZero } from '../utils/format/string';
import { getMonthEndDay } from './utils';
import { sharedProps, TimePickerMixin,getMonthWeekNumByDate } from './shared';

const currentYear = new Date().getFullYear();
const [createComponent] = createNamespace('date-picker');

export default createComponent({
  mixins: [TimePickerMixin],

  props: {
    ...sharedProps,
    type: {
      type: String,
      default: 'date',
    },
    format:{
      type: String,
      default: 'yyyy-MM-dd',
    },
    valueFormat: { //针对的是绑定值value的格式定义
      type: String,
    },
    minDate: {
      type: Date,
      default: () => new Date(currentYear - 100, 0, 1),
      validator: isDate,
    },
    maxDate: {
      type: Date,
      default: () => new Date(currentYear + 100, 11, 31),
      validator: isDate,
    },
  },

  watch: {
    filter: 'updateInnerValue',
    minDate: 'updateInnerValue',
    maxDate: 'updateInnerValue',

    value(val) {
      // 清空后值为null，不重新设置picker的选中项
      if (val === null) return;
      val = this.formatValue(val);

      if (val.valueOf() !== this.innerValue.valueOf()) {
        this.innerValue = val;
      }
    },
  },

  computed: {
    actualFirstDayOfWeek(){
       return this.pickerOptions == undefined || this.pickerOptions.firstDayOfWeek == undefined ? 7 : this.pickerOptions.firstDayOfWeek%7;
    },
    ranges() {
      const {
        maxYear,
        maxDate,
        maxMonth,
        maxHour,
        maxMinute,
        maxSecond,
      } = this.getBoundary('max', this.innerValue);

      const {
        minYear,
        minDate,
        minMonth,
        minHour,
        minMinute,
        minSecond,
      } = this.getBoundary('min', this.innerValue);

      const result = [
        {
          type: 'year',
          range: [minYear, maxYear],
        },
        {
          type: 'month',
          range: [minMonth, maxMonth],
        },
        {
          type: 'day',
          range: [minDate, maxDate],
        },
        {
          type: 'hour',
          range: [minHour, maxHour],
        },
        {
          type: 'minute',
          range: [minMinute, maxMinute],
        },
        {
          type: 'second',
          range: [minSecond, maxSecond],
        },
      ];
      if (this.type === 'year') result.splice(1, 5);
      if (this.type === 'month' || this.type === 'monthrange') result.splice(2, 4);
      if (this.type === 'date'||this.type === 'daterange') result.splice(3, 3);
      if (this.type === 'week'){
        result.splice(2, 4);
        result.push({ type: 'week',range:[1,this.getMonthWeekCount(this.innerValue.getFullYear(),this.innerValue.getMonth()+1)]});
      }

      if(((this.type === 'month' || this.type === 'monthrange') && this.format !="yyyy-MM") || ((this.type === 'date' || this.type === 'daterange') && this.format !="yyyy-MM-dd") || ((this.type === 'datetime' || this.type === 'datetimerange') && this.format !="yyyy-MM-dd HH:mm:ss")){
        for(let i=result.length-1;i>=0;i--){
          const currType = result[i].type;
          if(currType == "second" && this.format.indexOf("ss")<0){
            result.splice(i,1);
          }
          else if(currType == "minute" && this.format.indexOf("mm")<0){
            result.splice(i,1);
          }
          else if(currType == "hour" && this.format.indexOf("HH")<0){
            result.splice(i,1);
          }
          else if(currType == "day" && this.format.indexOf("dd")<0){
            result.splice(i,1);
          }
          else if(currType == "month" && this.format.indexOf("MM")<0){
            result.splice(i,1);
          }
          else if(currType == "year" && this.format.indexOf("yy")<0){
            result.splice(i,1);
          }
        }
      }
      return result;
    },
  },

  methods: {
    formatValue(value) {
      value = typeof value == "object" && VueUtil.isDate(value) ? value : !value || typeof value == "string" && VueUtil.isEmpty(value) ? new Date() : VueUtil.parseDate(value,this.valueFormat);

      value = Math.max(value, this.minDate.getTime());//比较两者取其大
      value = Math.min(value, this.maxDate.getTime());//比较两者取其小

      return new Date(value);
    },

    getBoundary(type, value) {
      const boundary = this[`${type}Date`];
      const year = boundary.getFullYear();
      let month = 1;
      let date = 1;
      let hour = 0;
      let minute = 0;
      let second = 0;

      if (type === 'max') {
        month = 12;
        date = getMonthEndDay(value.getFullYear(), value.getMonth() + 1);
        hour = 23;
        minute = 59;
        second = 59;
      }

      if (value.getFullYear() === year) {
        month = boundary.getMonth() + 1;
        if (value.getMonth() + 1 === month) {
          date = boundary.getDate();
          if (value.getDate() === date) {
            hour = boundary.getHours();
            if (value.getHours() === hour) {
              minute = boundary.getMinutes();
            }
            if (value.getMinutes() === minute) {
              second = boundary.getSeconds();
            }
          }
        }
      }

      return {
        [`${type}Year`]: year,
        [`${type}Month`]: month,
        [`${type}Date`]: date,
        [`${type}Hour`]: hour,
        [`${type}Minute`]: minute,
        [`${type}Second`]: second,
      };
    },

    updateInnerValue() {
      let year,month,day,hour,minute,second,week = undefined;
      for(let i=0;i<this.originColumns.length;i++){
        const tmpType = this.originColumns[i].type;
        const tmpVal  = this.getPicker().getValues()[i].replace(/[^0-9]/ig,"");//parseInt(this.getPicker().getValues()[i]);
        if(tmpType == "year"){
          year = tmpVal;
        }else if(tmpType == "month"){
          month = tmpVal;
        }else if(tmpType == "day"){
          const maxDay = year && month ?getMonthEndDay(year, month):31;
          day = tmpVal;
          day = day > maxDay ? maxDay : day;
        }else if(tmpType == "hour"){
          hour = tmpVal;
        }else if(tmpType == "minute"){
          minute = tmpVal;
        }else if(tmpType == "second"){
          second = tmpVal;
        }else if(tmpType == "week"){
          week = tmpVal;
          const weekNum = this.getMonthWeekCount(year,month);
          day = this.getFirstDayOfWeekActualDay(year,month,week>weekNum?weekNum:week);
        }
      }
      const value = !day ? new Date(year?year:currentYear, month?month - 1:0): hour==undefined ? new Date(year?year:currentYear, month?month - 1:0,day) : minute==undefined?new Date(year?year:currentYear, month?month - 1:0,day,hour):second==undefined ? new Date(year?year:currentYear, month?month - 1:0,day,hour,minute):new Date(year?year:currentYear, month?month - 1:0,day,hour,minute,second);
      
      this.innerValue = this.formatValue(value);
    },

    onChange(picker) {
      this.updateInnerValue();

      this.$nextTick(() => {
        this.$nextTick(() => {
          this.$emit('change', picker);
        });
      });
    },
    updateColumnValue() {
      const value = this.innerValue;
      const { formatter} = this;
      const initValues=[
        {year:formatter('year', `${value.getFullYear()}`, value)},
        {month:formatter('month', padZero(value.getMonth() + 1), value)},
      ];
 
      if (this.type === 'week') {
        initValues.push({week:formatter('week', getMonthWeekNumByDate(value,this.actualFirstDayOfWeek), value)});
      }else if(this.type !== 'month' && this.type !== 'monthrange'){
        initValues.push({day:formatter('day', padZero(value.getDate()), value)});
  
        if (this.type === 'datetime' || this.type === 'datetimerange') {
          initValues.push({hour:formatter('hour', padZero(value.getHours()), value)});
          initValues.push({minute:formatter('minute', padZero(value.getMinutes()), value)});
          initValues.push({second:formatter('second', padZero(value.getSeconds()), value)});
        }
      }

      const values=[];
      for(let i=0;i<initValues.length;i++){
        const tmpVal = initValues[i];
        if(tmpVal.year && (!this.format || (this.format &&  this.format.indexOf("yy")>=0))){
          values.push(tmpVal.year);
        }else if(tmpVal.month && (!this.format || (this.format &&  this.format.indexOf("MM")>=0))){
          values.push(tmpVal.month);
        }else if(tmpVal.week){
          values.push(tmpVal.week);
        }else if(tmpVal.day && (!this.format || (this.format &&  this.format.indexOf("dd")>=0))){
          values.push(tmpVal.day);
        }else if(tmpVal.hour && (!this.format || (this.format &&  this.format.indexOf("HH")>=0))){
          values.push(tmpVal.hour);
        }else if(tmpVal.minute && (!this.format || (this.format &&  this.format.indexOf("mm")>=0))){
          values.push(tmpVal.minute);
        }else if(tmpVal.second && (!this.format || (this.format &&  this.format.indexOf("ss")>=0))){
          values.push(tmpVal.second);
        }
      }
       this.$nextTick(() => {
        this.getPicker().setValues(values);
      }); 
    },
    //获取选中周并属于当月的范围内的该周的第一天
    getFirstDayOfWeekActualDay(year, month,weekNum) {
      const date = new Date();

      // 该月第一天
      date.setFullYear(year, month-1, 1);

      //获取月份第一天是周几，周日为0
      const w1 = date.getDay();
      
      // 该月天数
      date.setFullYear(year, month, 0);
      // 第一个星期日
      const d1 = 7 - w1 + 1;

      let firstDay = d1+(weekNum-2)*7;
      firstDay = Math.max(firstDay, 1);//比较两者取其大
      firstDay = Math.min(firstDay, 31);//比较两者取其小
      // const actualDay = firstDay+(this.actualFirstDayOfWeek+1)%7;
      return firstDay;
    },
    getMonthWeekCount(year, month) {
      const date = new Date();

      // 该月第一天
      date.setFullYear(year, month-1, 1);

      //获取月份第一天是周几，周日为0
      const w1 = date.getDay();
       
      // 该月天数
      date.setFullYear(year, month, 0);
      let days = date.getDate();

      //要减去开头的几天
      let first =0;
      w1 ==0 ? first=1 : first = 8-w1;
      days = days - first;
      const weekCount = 1 + Math.ceil(days/7);
      return weekCount;
    },
  },
  
});
